.overlay {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition: all .2s linear;

}

.overlayVisible {
  visibility: visible;
  opacity: 1;
  
  .drawer {
    transform: translateX(0);
  }
}

.drawer {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 420px;
  right: 0;
  height: 100%;
  padding: 30px;
  transform: translateX(100%);


  background: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);

  h2 {
    margin: 0;
    font-size: 24px;

    img {
      cursor: pointer;
      transition: all .2s linear;
      &:hover {
        opacity: 1;
        transform: scale(1.1);
      }
    }
  }
}